.messagesender{
    display: flex;
    margin-top: 30px;
    flex-direction: column;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
}

.message_top{
    display: flex;
    border-bottom: 1px solid #eff2f5;
    padding: 15px;
}

.message_top > form{
    flex: 1;
    display: flex;
}

.message_top > form > input{
    outline-width: 0;
    border: none;
    padding: 5px 20px;
    margin: 0 10px;
    border-radius: 999px;
    background-color: #eff2f5;
}

.message_top > form > button{
    display: none;
}

.messagesender_input{
    flex: 1;
}

.message_bottom{
    display: flex;
    justify-content: space-evenly;
}

.message_option{
    padding: 20px;
    display: flex;
    align-items: center;
    color: gray;
    margin: 5px;
    cursor: pointer;
}

.message_option > h3{
    font-size: medium;
    margin-left: 10px;
}

.message_option:hover{
    background-color: #eff2f5;
    border-radius: 20px;
}