.post{
    width: 100%;
    margin-top: 15px;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
    background-color: white;
}

.post_top{
    display: flex;
    padding: 15px;
    position: relative;
    align-items: center;
}


.post_avatar{
    margin-right: 10px;
} 

.post_topInfo > h3{
    font-size: medium;
}

.post_topInfo > p{
    font-size: small;
    color: gray;
}

.post_bottom{
    margin-top: 10px;
    margin-bottom: 10px;

    padding: 15px 25px;
}



.post_image > img{
    width: 100%;
}


.post_options{
    padding-top: 10px;
    border-top:  1px solid lightgray;

    display: flex;
    justify-content: space-evenly;
    color: gray;
    font-size: medium;
    cursor: pointer;
    padding: 15px;
}

.post_option{
    display: flex;
    flex: 1;
    justify-content: center;
    align-content: center;
    padding: 5px;
}

.post_option >p{
    margin-left: 10px;
}

.post_option:hover{
    background-color: #eff2f5;
    border-radius: 10px;
}